.wrapper{
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.block{
    position: relative;
    width: 500px;
    height: 500px;
    background-color: white;
    border-radius: 8px;
}
.close{
    position: absolute;
    top:0;
    right: 10px;
    font-size: 24px;
    font-weight: 700;
    color:brown;
    cursor: pointer;
}
h2{
    text-align: center;
    font-size: 32px;
}
.loginFieldsBlock{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: stretch;
    height: 80%;
}
.loginFieldsBlock div{
    text-align: center;
    padding: 0 10px;
}
.loginFieldsBlock input{
    padding: 15px 0;
    width: 100%;
    border: none;
    outline: none;
    border-bottom: 1px solid #ccc;
    font-size: 24px;
}